.star-ratings-css {
  unicode-bidi: bidi-override;
  color: #c5c5c5;
  font-size: 25px;
  height: 25px;
  width: 105px;
  position: relative;
  padding: 0;

  &-top {
    width: 100%;
    color: #ff901f;
    padding: 0;
    position: absolute;
    z-index: 1;
    display: block;
    top: 0;
    left: 0;
    overflow: hidden;
  }
  &-bottom {
    padding: 0;
    display: block;
    z-index: 0;
  }
}

.review {

  &:after {
    clear: both;
    content: "";
    display: table;
  }

  &__img {
    float: left;
    width: 100px;

    & > img {
      border-radius: 50%;
    }

  }

  &__content {
    margin-left: 120px;
    line-height: normal;
  }

  &__text {
    display: inline-block;
    padding-top: 10px;
  }

  &__date {
    display: inline-block;
    font-size: 12px;
    color: #777;
  }

}
