.about {

  &__info {
    max-width: 1200px;
    margin: 0 auto;
    padding: 40px 20px 0 20px;
    line-height: normal;
    background: #fff;
    color: #333;

    &:after {
      clear: both;
      content: "";
      display: table;
    }



    & h1 {
      margin: 0 20px 20px 0;
      font-size: 30px;
      font-weight: 400;
    }

    & h2 {
      margin: 0 20px 20px 0;
      font-size: 26px;
      font-weight: 400;
    }

    & p {
      font-size: 16px;
      line-height: 30px;
    }

    & center {
      text-align: center;
    }

  }

  &__contact-info {
    line-height: 24px;
    font-size: 14px;
    padding-bottom: 10px;
  }

  &__contact {

    & h3 {
      font-weight: 600;
    }

    & a {
      color: #0277BD;
      text-decoration: none;
    }

  }

  &__form {
      font-size: 16px;
      background:#fff;
      padding: 10px;

    & h3 {
      text-align: center;
      font-weight: 400;
      padding: 10px 20px 20px 20px;
      margin: 0;
    }

    & input, & textarea {
      font-family: 'Open Sans', sans-serif;
      font-size: 14px;
      width:100%;
      border:1px solid #CCC;
      border-radius: 3px;
      background:#FFF;
      margin:0 0 5px;
      padding: 10px;
      outline: 0;
      transition:border-color 0.2s ease-in-out;

      &:hover {
        border: 1px solid #AAA;
      }

      &:focus {
        border: 1px solid #3BC391;
      }

    }

    & button {
      color: #fff;
      font-weight: 300;
      display: block;
      width: 100%;
      padding: 20px 40px;
      background: #3BC391;
      border-radius: 3px;
      border: none;
      text-decoration: none;
      transition: all 0.3s cubic-bezier(.25,.8,.25,1);
      outline: 0;
      cursor: pointer;

      &:hover {
        background: #3BC391;
        box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
      }

      &:active {
        background: #33a97e;
      }

    }

  }

}
